/*   */
#jm-step1.jm-step1-wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #f9f9f9;
    font-family: 'Arial', sans-serif;
}

#jm-step1 #container {
    width: 100%;
    background-color: #ffffff;
    overflow: hidden;
}

#jm-step1 #board {
    text-align: center;
}

/*   */
#jm-step1 #board h2 {
    margin-block: 40px;
    font-size: 24px;
    font-weight: bold;
    color: #333333;
    text-align: center;
    margin-bottom: 20px;
}

/*    */
#jm-step1 #list {
    display: flex;
    justify-content: center; /*     */
    align-items: center; /*      */
    margin: 0 auto; /*     */
    text-align: center;
    width: 40%; /*    */
    gap: 16px;
    margin-bottom: 30px;
    margin-top: 30px;
}

#jm-step1 #list #list-left,
#jm-step1 #list #list-right {
    flex: 0 1 45%; /*       */
    height: 400px;
    padding: 30px 20px;
    background-color: #ffffff; /*    */
    border: 2px solid #333333; /*    */
    border-radius: 6px;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    text-align: center;
    box-sizing: border-box; /*     */
}

/*   */
#jm-step1 #list #list-left.selected,
#jm-step1 #list #list-right.selected {
    background-color: #ffffff; /*     */
    border: 2px solid #007bff; /*     */
}

/*    */
#jm-step1 #list #list-left:not(.selected):hover,
#jm-step1 #list #list-right:not(.selected):hover {
    background-color: #e2e2e2; /*         */
}

/*   */
#jm-step1 #list #list-left:hover,
#jm-step1 #list #list-right:hover {
    background-color: #ffffff;
    border: 2px solid #007bff;
}

/*   */
#jm-step1 #list h3 {
    font-size: 22px;
    margin-bottom: 40px;
    font-weight: bold;
}

/*   */
#jm-step1 #list ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

#jm-step1 #list ul li {
    display: flex;
    align-items: center;
    font-size: 15px;
    margin-bottom: 20px;
}

#jm-step1 #list ul li span:first-child {
    margin-right: 8px;
    color: #ffffff;
}

#jm-step1 #list #list-left ul li span:first-child,
#jm-step1 #list #list-right ul li span:first-child {
    color: inherit; /*      */
}

/*   */
#jm-step1 .btn {
    display: flex;
    justify-content: center; /*    */
    gap: 12px;
    margin-top: 20px; /*      */
}

/*   */
#jm-step1 .btn #secondary {
    padding: 12px 24px;
    background-color: #e0e0e0;
    color: #333333;
    border: none;
    border-radius: 3px;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

#jm-step1 .btn #secondary:hover {
    background-color: #c6c6c6;
}

#jm-step1 .btn #primary {
    padding: 12px 24px;
    background-color: #d3d3d3; /*    */
    color: #ffffff;
    border: none;
    border-radius: 3px;
    font-size: 16px;
    cursor: not-allowed;
    transition: background-color 0.3s ease, transform 0.2s;
}

/*    */
#jm-step1 .btn #primary.active {
    background-color: #007bff; /*   */
    cursor: pointer;
    transform: scale(1.05); /*   */
}

#jm-step1 .btn #primary.active:hover {
    background-color: #0056b3; /*    */
}

/*   */
@media screen and (max-width: 768px) {
    #jm-step1 #list {
        flex-direction: column;
    }

    #jm-step1 #list #list-left,
    #jm-step1 #list #list-right {
        margin-bottom: 16px;
    }
}
