@charset "UTF-8";
/* src/ham_asset/css/ham_mypage_ranking.css */

/* .ham_wrap .hmk_rank_main */

/********************   wrap  ********************/
.hmk_rank_page .hmk_rank_title_banner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    background-size: cover;
    background-position: center;
    opacity: 0.9;
    color: white;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000;
    font-size: 2rem;
    padding: 0 20px;
    text-align: center;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.hmk_rank_page .hmk_rank_title_banner::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
}

/*    */
.hmk_rank_page .hmk_rank_title_banner h2 {
    color: #FFD700;
    font-size: 36px;
    font-weight: 700;
    text-shadow:
        2px 2px 0 #000,
        -2px -2px 0 #000,
        2px -2px 0 #000,
        -2px 2px 0 #000;
    letter-spacing: 2px;
    transition: font-size 0.3s ease;
}

/*     */
@keyframes moveText {
    0% {
        transform: translateX(150%);
    }

    100% {
        transform: translateX(-150%);
    }
}

/*   */
.hmk_rank_page .hmk_rank_title_banner .hmk_rank_moving_text {
    display: flex;
    align-items: center;
    animation: moveText 20s linear infinite;
    /*    */
    white-space: nowrap;
    /*      */
}

/*   gif  */
.hmk_rank_title_banner .hmk_rank_moving_text .hmk_rank_banner_icon {
    width: 80px;
    height: 80px;
    margin: 0 20px;
    vertical-align: middle;
}

/*    */
.hmk_rank_title_banner .hmk_rank_moving_text span {
    display: inline-flex;
    align-items: center;
    margin-left: 30px;
    font-size: 2rem;
}

/*    */
.hmk_rank_title_banner .hmk_rank_moving_text .hmk_rank_banner_prof {
    border-radius: 50%;
    width: 25px;
    height: 25px;
    margin: 5px 10px;
    padding-top: 5px;
}

/******************** (/  wrap ) ********************/

.hmk_rank_main::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    filter: blur(5px);
    opacity: 0.8;
    /*    */
    z-index: -1;
    /*     */
}

/*********************     *********************/
/*  1  */
.hmk_rank_main {
    width: 75vw;
    margin: 0 auto;
    padding: 40px 0;
}

/*      */
/*       */
.hmk_rank_main .hmk_rank_head {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    margin-bottom: 40px;
    align-items: center;
    backdrop-filter: blur(2px);
    /*   */
}

.hmk_rank_main .hmk_rank_head::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 75vw;
    height: 100%;
    /*      */
    background-size: cover;
    filter: blur(2px);
    /*     */
    opacity: 1;
    /*     */
    z-index: -1;
    /*    */
}

/*     */
.hmk_rank_head_container {
    display: flex;
    margin-bottom: -150px;
    margin-top: -150px;
}

/*      */
.hmk_ranker_podiumframe {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

/*      */
.hmk_ranker_podiumframe 
.hmk_rank_podium {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    gap: 1px;

}

/*    */
.hmk_ranker_podiumframe 
.hmk_rank_podium 
.hmk_podium_rank {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 20px 20px 0 20px;
    margin: 10px;
    text-align: center;
    transition: transform 0.3s ease-in-out;
    align-items: center;
}

.hmk_rank_podium .hmk_rank1 {
    background: linear-gradient(to bottom, #ffffff, #FFD700);
    /*   */
    border-radius: 5px;
    opacity: 0.9;
}

.hmk_rank_podium .hmk_rank2 {
    background: linear-gradient(to bottom, #ffffff, #C0C0C0);
    /*   */
    border-radius: 5px;
    opacity: 0.9;
}

.hmk_rank_podium .hmk_rank3 {
    background: linear-gradient(to bottom, #ffffff, #CD7F32);
    /*   */
    border-radius: 5px;
    opacity: 0.9;
}

.hmk_podium_rank:hover {
    transform: scale(1.05) translateY(-10px);
    /* 1.1  */
}

/*       */
.hmk_podium_rank .hmk_podium_profile {
    display: flex;
    flex-direction: column;
    gap: 1px;
    align-items: center;
    text-align: center;
    position: relative;
    margin-bottom: 25px;
}
.hmk_gold_frame {
    width: 160px; /*   */
    height: 188px;
    top: -45px;
    background-size: 100%;
    background-position: bottom;
    background-repeat: no-repeat;
}
.hmk_silver_frame {
    width: 123px; 
    height: 145px;
    top: -20px;
    background-size: 100%;
    background-position: bottom;
    background-repeat: no-repeat;
}

.hmk_bronze_frame {
    width: 135px; 
    height: 135px;
    top:-15px;
    background-size: 100%;
    background-position: bottom;
    background-repeat: no-repeat;
}


/*       */
.hmk_podium_profile .hmk_ranker_profile_no1 {
    padding-top: 68px; /*      */
    padding-left: 8px ;
    width: 95px; /*   */
    height: 95px;
    border-radius: 50%;
    margin-bottom: 60px;
    /*     */
    z-index: -1;
}


.hmk_podium_profile .hmk_ranker_profile_no2 {
    padding-top: 40px; /*      */
    padding-left: 6px ;
    width: 90px; /*   */
    height: 90px;
    border-radius: 50%;
    margin-bottom: 30px;
    /*     */
    z-index: -1;
}

.hmk_podium_profile .hmk_ranker_profile_no3 {
    padding-top: 33px; /*      */
    padding-left: 5px ;
    width: 85px; /*   */
    height: 85px;
    border-radius: 50%;
    margin-bottom: 25px;
    /*     */
    z-index: -1;
}


/*      */
.hmk_podium_medal {
    font-size: 40px;
}

/*        */
.hmk_podium_rank .hmk_podium_base {
    text-align: center;
    padding: 15px 0;
    border-radius: 10px 10px 0 0;
    font-weight: bold;
    color: white;
    margin-top: 20px;
    font-size: 30px;
}

/*       */
.hmk_podium_base p:last-child {
    font-size: 1.2rem;
    font-weight: bold;
    color: #344767;
    margin-top: 5px;
}

/* 1   */
.hmk_rank1 .hmk_podium_base {
    height: 250px;
    width: 250px;
    background-color: #FFD700;
    /*   */
    background-size: 40%;
    background-position: bottom;
    background-repeat: no-repeat;
}

/* 2   */
.hmk_rank2 .hmk_podium_base {
    height: 160px;
    width: 100%;
    background-color: #C0C0C0;
    /*   */
    background-size: 30%;
    background-position: bottom;
    background-repeat: no-repeat;
}

/* 3   */
.hmk_rank3 .hmk_podium_base {
    height: 140px;
    width: 100%;
    background-color: #CD7F32;
    /*   */
    background-size: 30%;
    background-position: bottom;
    background-repeat: no-repeat;
}

/********************* /    *********************/



/*********************    *********************/
/*    */
.hmk_rank_main .hmk_rank_body {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.hmk_rank_body {
    position: relative;
    width: 100%;
    padding: 20px;
    /* z-index: -1; */
    /*      */

    /*      */
    backdrop-filter: blur(7px);
    background-color: rgba(255, 255, 255, 0.1);
    /*      */
}

/*      */
.hmk_rank_my,
.hmk_rank_top10 {
    width: 90%;
    margin-top: 40px;
    text-align: center;
}

/*     2 */
.hmk_rank_my_rank,
.hmk_rank_list {
    width: 100%;
    table-layout: fixed;
    height: fit-content;
    border-collapse: collapse;
    margin: 0 auto;
    background-color: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    overflow: hidden;
}

/*    ,       */
.hmk_rank_list th,
.hmk_rank_list td .hmk_rank_my_rank th,
.hmk_rank_my_rank tbody .hmk_rank_my_rank_number td {
    border-bottom: 1px solid #ddd;
    padding: 15px;
    text-align: center;
    font-size: 16px;
    height: fit-content;
}

/*     */
.hmk_rank_my_rank_number {
    background: linear-gradient(to top, #f4f4f4c7, #323232c2);
    /* */
    color: white;
    /**/

}

.hmk_rank_my_rank_number .hmk_rank_my_rank_nickname {
    font-size: 40px;
}

.hmk_rank_list th {
    background-color: #4CAF50;
    color: white;
    font-weight: bold;
}

.hmk_rank_list td:first-child {
    font-size: 40px;
    font-weight: 100;
}

.hmk_rank_list td img,
.hmk_rank_my_rank td img {
    height: 50px;
    border-radius: 50%;
}

.hmk_rank_list tr:nth-child(even) {
    background-color: #f2f2f2;
}

.hmk_rank_list tr:hover {
    background-color: #e8f5e9;
}




.hmk_rank_list span, img{
    cursor: pointer;
}