@charset "UTF-8";

/* ham_M_pointstore.css */

/*     */
.hmk_mobile_pointstore {
    margin-top: -70px;
    flex: 1;
    background-color: #f2f2f7;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    width: 100%;
    position: relative;
}

/*     */
.hmk_mobile_pointstore-sticky-container {
    position: fixed;
    top: 110px;
    /* fixed-top   */
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #f2f2f7;
    max-width: 768px;
    margin: 0 auto;
}

/*    */
.hmk_mobile_pointstore-header {
    padding: 16px;
    background-color: white;
    display: flex;
    align-items: center;
    gap: 8px;
    border-bottom: 1px solid #eee;
    box-sizing: border-box;
    width: 100%;
}

/*   */
.hmk_mobile_pointstore-categories {
    padding: 12px 16px;
    display: flex;
    overflow-x: auto;
    gap: 8px;
    background-color: white;
    margin-bottom: 12px;
    box-sizing: border-box;
    width: 100%;
}

/*      */
.hmk_mobile_pointstore-items {
    margin-top: 140px;
    /* sticky-container    */
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;
    padding: 16px;
    padding-bottom: 80px;
    box-sizing: border-box;
    width: 100%;
}

/*   */
.hmk_mobile_pointstore-point-icon {
    width: 24px;
    height: 24px;
}

.hmk_mobile_pointstore::-webkit-scrollbar {
    display: none;
    /* Chrome, Safari, Opera */
}


/*   */
.hmk_mobile_pointstore-category {
    padding: 8px 16px;
    border-radius: 20px;
    border: 1px solid #ddd;
    background: none;
    white-space: nowrap;
    font-size: 14px;
    flex-shrink: 0;
    /*     */
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

/*   */
.hmk_mobile_pointstore-category.active {
    background-color: #007AFF;
    color: white;
    border-color: #007AFF;
}

/*    */
.hmk_mobile_pointstore-item {
    background: white;
    border-radius: 12px;
    overflow: hidden;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    width: 100%;
    cursor: pointer;
    transition: transform 0.2s;
}

.hmk_mobile_pointstore-item:hover {
    transform: scale(1.02);
}

/*    */
.hmk_mobile_pointstore-item-image {
    position: relative;
    padding-top: 100%;
    /*    */
    width: 100%;
    box-sizing: border-box;
}

/*   */
.hmk_mobile_pointstore-item-image img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/*    */
.hmk_mobile_pointstore-item-purchased {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    font-weight: 600;
}

/*   */
.hmk_mobile_pointstore-item-info {
    padding: 12px;
    box-sizing: border-box;
    width: 100%;
}

/*   */
.hmk_mobile_pointstore-item-info h3 {
    font-size: 14px;
    margin-bottom: 4px;
    color: #333;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/*   */
.hmk_mobile_pointstore-item-info p {
    font-size: 16px;
    font-weight: 600;
    color: #007AFF;
    margin: 0;
}

/*   */
.hmk_mobile_pointstore-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/*   */
.hmk_mobile_pointstore-modal {
    background-color: white;
    border-radius: 12px;
    padding: 20px;
    width: 80%;
    max-width: 320px;
    box-sizing: border-box;
    overflow: hidden;
    /*     */
}

/*   */
.hmk_mobile_pointstore-modal h3 {
    font-size: 18px;
    margin-bottom: 16px;
    text-align: center;
}

/*   */
.hmk_mobile_pointstore-modal p {
    margin: 8px 0;
    text-align: center;
}

/*    */
.hmk_mobile_pointstore-modal-info {
    margin: 20px 0;
    text-align: center;
    padding: 12px;
    background-color: #f5f5f5;
    border-radius: 8px;
}

/*    */
.hmk_mobile_pointstore-modal-buttons {
    display: flex;
    justify-content: center;
    /*    */
    gap: 12px;
    /*    */
    margin-top: 20px;
}

/*     */
.hmk_mobile_pointstore-modal button {
    padding: 12px;
    border: none;
    border-radius: 8px;
    background-color: #007AFF;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s;
}

/*    */
.hmk_mobile_pointstore-modal button:first-child {
    background-color: #8e8e93;
}

.hmk_mobile_pointstore-modal button:hover {
    transform: scale(1.05);
}

/*  ( )  */
.hmk_mobile_pointstore-modal-single-button {
    display: flex;
    justify-content: center;
    /*   */
    margin-top: 20px;
}

/*    */
.hmk_mobile_pointstore-modal-single-button button {
    width: 50%;
    /*    */
    padding: 12px;
    border: none;
    border-radius: 8px;
    background-color: #007AFF;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s;
}

.hmk_mobile_pointstore-modal-single-button button:hover {
    transform: scale(1.05);
}

/* iOS  Alert  */
.hmk_mobile_pointstore-alert-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2000;
    animation: fadeIn 0.2s ease-out;
}

.hmk_mobile_pointstore-alert {
    background-color: rgba(255, 255, 255, 0.95);
    width: 270px;
    border-radius: 14px;
    overflow: hidden;
    animation: scaleIn 0.2s ease-out;
}

.hmk_mobile_pointstore-alert-content {
    padding: 16px;
    text-align: center;
}

.hmk_mobile_pointstore-alert-message {
    font-size: 13px;
    color: #000000;
    margin: 0;
    padding: 4px 0;
}

.hmk_mobile_pointstore-alert-button {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    padding: 12px;
    text-align: center;
    background: none;
    border: none;
    color: #007AFF;
    font-size: 17px;
    font-weight: 600;
    width: 100%;
    cursor: pointer;
}

.hmk_mobile_pointstore-alert-button:active {
    background-color: rgba(0, 0, 0, 0.1);
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes scaleIn {
    from {
        transform: scale(1.2);
        opacity: 0;
    }

    to {
        transform: scale(1);
        opacity: 1;
    }
}