@charset "UTF-8";
/* src/ham_asset/css/ham_mypage_cargo.css */

/*    */
.wrap .hmk_main-container .hmk_main .hmk_cargobox {
    margin-top: 20px;
    /*    */
}

.wrap .hmk_main-container .hmk_main .hmk_cargobox h1 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}

.wrap .hmk_main-container .hmk_main .hmk_gift-gallery {
    display: flex;
    flex-direction: row;
    gap: 20px;

}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-card {
    position: relative;
    /*       */
    max-width: 262px;
    height: 322px;
    /*   */
    overflow: hidden;
    /*        */
    border-radius: 8px;
    /*    */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame {
    position: relative;
    /*  */
    width: 100%;
    height: 260px;
    border-radius: 12px;
    /* box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1); */
    transition: all 0.3s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}


/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    transition: all 0.3s ease;
    /*  */

}

.wrap .hmk_main-container .hmk_main .hmk_gift-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: all 0.3s ease;
    pointer-events: none;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_gift-name-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 8px 16px;
    background-color: #3a7afe;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    opacity: 0;
    transition: all 0.3s ease;
    z-index: 2;
    font-size: 17px;
    /*     */
    white-space: normal;
    /*   */
    word-break: keep-all;
    /*    */
    word-wrap: break-word;
    /*    */
    min-width: 120px;
    /*    */
    max-width: 80%;
    /*    */
    text-align: center;
    /*    */
    line-height: 1.4;
    /*   */

}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame:hover .hmk_gift-image {
    filter: blur(3px);
    transform: scale(1.05);
}

.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame:hover .hmk_gift-overlay {
    background: rgba(0, 0, 0, 0.3);
}

.wrap .hmk_main-container .hmk_main .hmk_gift-image_frame:hover .hmk_gift-name-button {
    opacity: 1;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_gift-name-button:hover {
    background-color: #2f65e0;
    transform: translate(-50%, -50%) scale(1.05);
}

/* **   ** */
.hmk_cargo-tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

/*     */
.hmk_cargo-tab-button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #666;
    background-color: #f1f1f1;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

.hmk_cargo-tab-button:last-child {
    color: #666;
    background-color: #f0f0f0;
}

.hmk_cargo-tab-button:last-child:hover {
    color: #666;
    background-color: #e0e0e0;
}

.hmk_active-tab:last-child {
    background-color: #838383;
    color: white;
}

.hmk_cargo-tab-button:first-child {
    color: #ffffff;
    background-color: #00b3ff;
}

.hmk_cargo-tab-button:first-child:hover {
    background-color: #3065d0;
    color: white;
}

.hmk_active-tab:first-child {
    background-color: #3a7afe;
    color: white;
}

/* **   ** */
.hmk_no-gifts {
    font-size: 16px;
    color: #888;
    margin-top: 20px;
    width: 100%;
    text-align: center;
}

/*    */
.hmk_cargo_detail-modal {
    position: fixed;
    /*   */
    top: 0;
    left: 0;
    width: 100%;
    /*   */
    height: 100%;
    /*   */
    background-color: rgba(0, 0, 0, 0.5);
    /*    */
    display: flex;
    /* Flexbox  */
    justify-content: center;
    /*    */
    align-items: center;
    /*    */
    z-index: 1200;
    /*     */
}

/*     */
.hmk_cargo_detail-modal-content {
    background-color: #ffffff;
    /*   */
    padding: 20px;
    /*   */
    border-radius: 8px;
    /*   */
    width: 90%;
    /*  */
    max-width: 300px;
    /*   */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    /*   */
    position: relative;
    /*   */
    text-align: center;
    /*    */
}

/*       */
.hmk_cargo_detail-content {
    margin-bottom: 15px;
    /*   */
    font-size: 1.2em;
    /*   */
    color: #333333;
    /*   */
}

/*     */
.hmk_cargo_detail-image {
    width: 100%;
    /*   */
    height: auto;
    /*   */
    margin-bottom: 15px;
    /*   */
    border-radius: 4px;
    /*    */
    object-fit: scale-down;
    /*    */
}

/*      */
.hmk_cargo_detail-actions {
    display: flex;
    /* Flexbox  */
    justify-content: center;
    /*    */
    gap: 10px;
    /*    */
}

/*     */
.hmk_cargo_btnmodal {
    width: 30%;
    padding: 10px;
    background-color: #3a7afe;
    color: #ffffff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1em;
    transition: background-color 0.3s ease;
}

.hmk_cargo_btnmodal:last-child {
    background-color: #383E47;
    /*     */
    color: #fff;
}

.hmk_cargo_btnmodal[disabled] {
    background-color: #cccccc;
    cursor: not-allowed;
}

.hmk_cargo_btnmodal:hover {
    background-color: #3569c1;
    /*     */
}

.hmk_cargo_btnmodal:last-child:hover {
    background-color: #a32415;
    /*     */
    color: #fff;
}

/*      */
.hmk_close-modal {
    /*      */
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

.hmk_cargobox h1 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}

