/*   */
.hmk_modal_content {
    background: white;
    padding: 24px;
    border-radius: 12px;
    width: 90%;
    max-width: 320px;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

/*   */
.hmk_modal_message {
    font-size: 16px;
    line-height: 1.5;
    color: #333;
    margin-bottom: 20px;
    text-align: center;
    word-break: keep-all;
    padding: 0 12px;
}

/*   */
.hmk_modal_buttons {
    display: flex;
    justify-content: center;
    gap: 12px;
    padding-top: 8px;
}

/*    */
.hmk_modal_buttons button {
    flex: 1;
    min-width: 100px;
    padding: 10px 20px;
    border: none;
    border-radius: 6px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
}

/*   */
.hmk_confirm_btn {
    background-color: #3a7afe;
    color: white;
}

.hmk_confirm_btn:hover {
    background-color: #2f65e0;
}

/*   */
.hmk_cancel_btn {
    background-color: #f3f4f6;
    color: #4b5563;
}

.hmk_cancel_btn:hover {
    background-color: #e5e7eb;
}

/*    ( ) */
.hmk_modal_buttons button:only-child {
    max-width: 120px;
}

/*   */
.hmk_modal_overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    z-index: 1999;
}

/*    */
.hmk_gift-card.completed .hmk_gift-image_frame {
    position: relative;
}

.hmk_gift-card.completed .hmk_gift-image_frame::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 12px;
    z-index: 2;
}

/*      */
.hmk_gift-card.completed .hmk_gift-image_frame:hover {
    transform: none;
}

.hmk_gift-card.completed .hmk_gift-image_frame:hover .hmk_gift-image {
    filter: none;
    transform: none;
}

.hmk_gift-card.completed .hmk_gift-image_frame:hover .hmk_gift-name-button {
    opacity: 0;
}