/*   */
.jm-bady {
  display: flex;
  width: 80vw; /*   80%    */
  margin: 0 auto; /*   */
  min-height: 100vh;
  box-sizing: border-box;
  padding: 20px; /*     */
  background-color: #ffffff; /*   */
  flex-direction: column;

}


/*   */
.jm-mission-body {
  flex: 1;
  margin-left: 110px; /*   +  padding (120px + 40px) */
  margin-right: 110px; /*   */
  box-sizing: border-box; /*     */
  padding: 0 20px; /*   */
}

/* Header */


.jm-c-tatle-box {
  flex-grow: 1; 
}

.jm-c-tatle {
  text-align: left;
  font-size: 35px;
  font-weight: bold;
}

/*   */
.jm-sub-tatle {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
}

.jm-mission-container-top {
  display: flex;
}

.jm-mission-progress-bar-container {
  flex-grow: 1;
  flex-basis: 0;
}

.jm-calendar-container {
  flex-grow: 1;
  flex-basis: 0;
}

.jm-calendar {
}

/*    */
.jm-my-count {
  font-size: 18px;
  font-weight: bold;
}

/*    */
.jm-progress-bar {
  width: 95%;
  background: #ccc;
  height: 10px;
  border-radius: 5px;
  margin: 10px 0;
}

/*   */
.jm-progress {
  background: rgb(52, 73, 94);
  height: 100%;
  border-radius: 5px;
}

/*   */
.jm-roolset {
  padding-block: 20px;
}

.jm-roolset-update-contents-box {
  width: 92%;
  
}

.jm-roolset-contents-box {
  width: 100%;
  background-color: linear-gradient(135deg, #ffffff, #f7f9fa); /*   */
  padding: 10px;
  margin-right: 20px;
  margin-bottom: 15px;
  border-radius: 4px;
  display: flex;
  min-width: 200px; /*    */
  height: 150px;
  flex-direction: column;
  gap: 10px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);

}

.jm-roolset-contents-box span {
  background-color: white;
  color: #727272;
  width: 100%;
  height: 150px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box; /* Prevent overflow */
    
}

.jm-roolset-contents-box textarea {
  background-color: white;
  color: #727272;
  width: 100%;
  height: 150px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  font-size: 16px; /* Prevent overflow */
}

.jm-roolset-contents-box h3 {
  text-align: left;
  margin: 0px;
  font-size: 19px;
  font-weight: bold;
  color: black;
}
/*  ,  */
.jm-btn-updatimg button{
  float: right;
  margin-top: 5px;
  padding: 5px 10px;
  border: none;
  background-color: #007bff88;
  font-size: 14px;
  color: white;
  border-radius: 4px;
  cursor: pointer;
}
.jm-btn-updatimg button:hover{
  background-color: #007aff;
    color: #ffffff;
}

/*    */
.jm_challenge_statistics_additional-graph {
  width: 90%;
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 15px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  height: 200px; /*    */
  min-width: 200px; /*    */
  animation: fadeIn 0.5s ease;
}

.jm_challenge_statistics_additional-graph h2 {
  margin: 0px;
  font-size: 19px;
  font-weight: bold;
  color: black;
}

.jm-rule-input {
  width: 100%;
  padding-block: 10px;
  margin-top: 5px;
  border: none;
  border-radius: 4px;
  font-size: 16px;
}


/* =================================================================*/
.jm-todo {
  display: flex;
  margin: 0px;
  font-size: 28px;
  font-weight: bold;
}

.jm-todo-user-add-form {
  display: flex;
  margin-top: 20px;
  width: 100%;
}

.jm-mission-add-list {
  flex-grow: 1;
  border: solid 1px #ccc;
  border-radius: 5px;
  padding-block: 5px;
  padding-inline: 10px;
  margin-right: 20px;
}

.jm-mission-items {
  display: flex;
  border: solid 1px #ccc;
  border-radius: 3px;
  margin-block: 5px;
  padding: 10px;
  cursor: pointer; /*    */
  transition: background-color 0.3s ease;
}


.jm-mission-items:hover {
  background-color: #e9e9e9; /*     */
}

.jm-mission-items.selected {
  background-color: #d1e7ff; /*    */
  border-color: #007bff; /*     */
}

.jm-mission-items h2 {
  flex-grow: 1;
  font-size: 14px;
  font-weight: bold;
  margin-right: 20px;
}

.jm-view-button-container {
  text-align: right;
}

.jm-view-button {
  border: none;
  background-color: #555;
  border-radius: 3px;
  padding: 3px 6px;
  font-size: 12px;
  color: white;
}

.jm-file-upload {
  margin: 0px;
  width: 200px;
  margin-right: 10px;
}

.jm-file-upload-name {
  background-color: #727272;
  border-radius: 5px;
  width: 200px;
  height: 200px;
  white-space: nowrap; /*    */
  overflow: hidden; /*    */
  text-overflow: ellipsis; /*   ...  */

}

.jm-hidden-file-input {
  display: none; /*    */
  width: 100%;
  height: 100%;
}

.jm-file-label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  border: 2px dashed #ccc;
  border-radius: 8px;
  cursor: pointer;
  overflow: hidden;
  background-color: #f9f9f9;
}

.jm-add-mission-img-form {
  display: flex;
  height: 200px;
}

.jm-image-preview {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.jm-image-preview img {
  width: 100%;
}

.jm-placeholder-text {
  font-size: 14px;
  color: #aaa;
}

.jm-add-file-button {
  display: block;
  background: #383E47;
  color: white;
  font-size: 15px;
  border: none;
  padding: 5px 17px; 
  margin-top: 10px;
  cursor: pointer;
  border-radius: 4px;
}

.jm-add-file-button:hover {
  background-color: #5c5c5c;
}

.jm-comment-add-btn-container {
  display: flex;
  margin-top: 10px;
  align-items: flex-end; /*     */
}

.jm-add-comment-box {
  width: 400px;
  padding: 8px;
  font-size: 14px;
  line-height: 1.5;
  border: 1px solid #ccc;
  border-radius: 5px;
  height: 100px; /*         */
  resize: none; /*    */
  box-sizing: border-box;
  text-align: left; /*    */
  vertical-align: top; /*    */
}

.jm-add-comment-box::placeholder {
  text-align: left;
  vertical-align: top; /*   */
  color: #aaa;
}

.jm-add-user-mission-btn {
  display: block;
  background: #383E47;
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 15px;
  padding: 5px 17px; 
  height: 30px;
  cursor: pointer;
  margin-left: 10px;
}

.jm-add-user-mission-btn:hover {
  background-color: #5c5c5c;
}

.jm-mission-items {
  transition: opacity 0.3s ease;
}

.jm-mission-items.selected {
  border: 2px solid #007bff;
}

.jm-mission-items .submitted-label {
  color: red;
  font-weight: bold;
}

.jm-mission-items[style*="pointer-events: none"] {
  cursor: not-allowed;
}


