@charset "UTF-8";

/*  */
.dy-link {
    color: black; 
    text-decoration: none; /*   */
}

.dy-link:visited {
    color: black; 
}

.dy-link:hover {
    text-decoration: none; /*   */
}


/*   */
.dy-header {
    width: 90%;          /*    */
    height: 60px;
    margin: 0 auto;         /*   */
    border-bottom: 1px solid #d4d4d4;
}

.dy-header .dy-header-content {
    width: 75%;
    min-width: 1000px;
    height: 60px;
    margin: 0 auto;         /*   */
    display: flex;        /* flex  */
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

}

/*  */
.dy-header .dy-logo {
    margin-left: 10px;
    font-size: 18px;
}

/*   */
.dy-logo-container {
    display: flex;          /*     */
    align-items: left;    /*    */
}

/*    */
.dy-logo-icon {
    width: 40px;            /*    */
    height: 40px;
    padding-bottom:5px;
}

/*    */
.dy-logo-text {
    font-size: 18px;
    list-style: none;       /*   */
    padding: 13px 0px;
    margin: 0;
    align-items: center;
}

/*  */
.dy-header .dy-menu {
    width: 50%;
}

.dy-header .dy-menu ol {
    display: flex;        /* flex  */
    flex-direction: row;
    justify-content: space-between;
}

.dy-header .dy-menu ol>li {
    position: relative; /*    */
    /* overflow: hidden;       */
}

.dy-header .dy-menu ol > li::after {
    content: '';  /*    */
    position: absolute;
    bottom: -3px;  /*  li     */
    left: 0;
    width: 0;  /*   0 */
    height: 1px;  /*   */
    background-color: #5E91A7;  /*   */
    transition: width 0.3s ease;  /*  0 100%   */
}

.dy-header .dy-menu ol > li:hover::after {
    width: 100%;  /*    100%  */
}


/*     */
.dy-header .dy-info {
    width: 180px;
    align-self: right;
}

/*  */
.dy-header .dy-info .dy-beforelogin {
    display: flex;        /* flex  */
    flex-direction: row;
    justify-content: flex-end;
}

.dy-header .dy-info .dy-beforelogin>li {
    margin-right: 20px;
    position: relative; /*    */
}

.dy-header .dy-info .dy-beforelogin>li::after {
    content: '';  /*    */
    position: absolute;
    bottom: -3px;  /*  li     */
    left: 0;
    width: 0;  /*   0 */
    height: 1px;  /*   */
    background-color: #5E91A7;  /*   */
    transition: width 0.3s ease;  /*  0 100%   */
}

.dy-header .dy-info .dy-beforelogin>li:hover::after {
    width: 100%;  /*    100%  */
}



/*  */
.dy-header .dy-info .dy-afterlogin {
    display: flex;        /* flex  */
    flex-direction: row;
    justify-content: flex-start;
}

/*    */
.dy-header .dy-info .dy-afterlogin .dy-header-profile {
    width: 45px;
    height: 45px;
    border: 1px solid lightgray;
    border-radius: 5px;
    transition: transform 0.3s ease;  /*     */
}

/*       */
.dy-header .dy-info .dy-afterlogin .dy-header-profile:hover {
    transform: scale(1.1);  /* 1.1  */
}

.dy-header .dy-info .dy-afterlogin .dy-header-login-info {
    display: flex;        /* flex  */
    flex-direction: column;

    width: 100%;
    height: 48px;
    margin-left: 7px;
}

/*  */
.dy-header .dy-info .dy-afterlogin .dy-header-login-info .dy-header-nickname {
    font-size: 16px;
}

/*    */
/* .dy-header .dy-info .dy-afterlogin .dy-header-login-info .dy-header-pointNlogout {

} */

.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-header-point {
    float: left;
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    margin-top: 3px;
}

/*   */
.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-header-point img {
    width: 19px;
    height: 19px;
} 

/*   */
.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-header-point span {
    margin-left: 5px;
    font-size: 15px;
}

/*   */
.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-logout-btn {
    float: right;
    margin-top: 6px;
    font-size: 14px;
    border: none;
    background-color: white;
    position: relative;  /*::after     */
    
}

.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-logout-btn:hover {
    cursor: pointer;
}
.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-logout-btn::after {
    content: '';  /*    */
    position: absolute;
    bottom: -2px;  /*     */
    left: 0;
    width: 0;  /*   0 */
    height: 1px;  /*   */
    background-color: #5E91A7;  /*   */
    transition: width 0.3s ease;  /*  0 100%   */
}

.dy-afterlogin .dy-header-login-info .dy-header-pointNlogout .dy-logout-btn:hover::after {
    width: 100%;  /*    100%  */
}

.hmk_profile-container {
    position: relative; /*        */
    display: inline-block;
}

/*    */
.hmk_notification-link {
    position: absolute;
    top: -5px;
    right: -5px;
    text-decoration: none;
}


.hmk_notification-badge {
    position: absolute;
    top: 1px;
    right: -5px;
    background-color: red;
    color: white;
    border-radius: 50%;
    padding: 2px 6px;
    font-size: 10px;
    font-weight: bold;
    min-width: 8px;
    text-align: center;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
}

/*    (optional) */
.hmk_profile-link {
    position: relative;
    display: inline-block;
}