@charset "UTF-8";

/*     */
.wrap .hmk_main-container .hmk_main .hmk_notice_summary {
    margin-top: 20px;
}

.wrap .hmk_main-container .hmk_main .hmk_notice_summary h2 {
    margin: 20px;
    font-size: 20px;
    font-weight: bold;
}

.hmk_notice_info {
    display: flex;
    /*      */
    margin-bottom: 20px;
    /*    */
    gap: 10px;
}

.hmk_notice-box {
    width: 30%;
    /*     */
    padding: 20px;
    /*   */
    gap: 15px;
    background-color: #f4f4f4;
    /*   */
    border: 1px solid #ddd;
    /*   */
    border-radius: 8px;
    /*   */
    display: flex;
    /*      */
    flex-direction: column;
    /*    */
    align-items: center;
    /*    */
    text-align: center;
    /*    */
}

.hmk_notice-value {
    font-size: 24px;
    /*    */
    font-weight: bold;
    /*   */
}

.hmk_notice_filterbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    border-radius: 5px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.2);
}

.hmk_notice_filterbar .hmk_toggle-container {
    display: flex;
    /*   */
    margin-left: 30px;
    margin-bottom: 20px;
    /*    */
    border-radius: 20px;
    box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.05);
}

.hmk_notice-filter {
    display: flex;
    justify-content: flex-end;
    padding: 15px 15px 0 15px;
    height: 90px;
}

.hmk_notice-filter p {
    font-size: small;
    padding: 12px 0 12px 0;
}

.hmk_date-inputs {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 15px 0 15px 0;
}

.hmk_date-inputs .hmk_date {
    border-radius: 8px;
    /*   */
}

.hmk_date-inputs .hmk_date label {
    font-size: 10px;
}

/*     */
.hmk_date-options {
    margin-right: 10px;
    display: flex;
    flex-direction: row;
    font-size: 12px;
    align-content: center;
    flex-wrap: wrap;
    width: max-content;
}

.hmk_date-options .hmk_date-button {
    height: fit-content;
    margin-right: 5px;
    /*   */
    padding: 8px 10px;
    /*   */
    border: none;
    /*    */
    border-radius: 5px;
    /*   */
    background-color: #e9ecef;
    /*   */
    cursor: pointer;
    /*    */
    transition: background-color 0.3s;
    /*    */
}

/*    */
.hmk_date-options .hmk_date-button:hover {
    background-color: #f8d7da;
    /*     */
}

/*     */
.hmk_date-wrapper {
    position: relative;
    /*      */
    display: flex;
    /*      */
    align-items: center;
    /*    */
    margin-bottom: 15px;
    /*    */
}

.hmk_date-input {
    width: 150px;
    /*    */
    padding: 10px;
    /*   */
    margin-right: 10px;
    /*    */
    border: 1px solid #ccc;
    /*   */
    border-radius: 5px;
    /*   */
    outline: none;
}

/*    */
.hmk_search-button {
    padding: 10px 20px;
    /*   */
    background-color: #007bff;
    /*   */
    color: white;
    /*  */
    border: none;
    /*    */
    border-radius: 5px;
    /*   */
    cursor: pointer;
    /*    */
    transition: background-color 0.3s;
    /*    */
    width: max-content;
}

/*     */
.hmk_search-button:hover {
    background-color: #0056b3;
    /*     */
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_notice-table {
    width: 100%;
    /*   */
    table-layout: fixed;
    /*   */
    border-collapse: collapse;
    /*    */
    margin-top: 10px;
}

/*   */
.wrap .hmk_main-container .hmk_main .hmk_notice-table thead {
    border-bottom: 1px solid #ededed;
    background-color: #f8f8f8;
    /*   */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_notice-table th,
.wrap .hmk_main-container .hmk_main .hmk_notice-table td {
    padding: 10px;
    /*    */
    text-align: center;
    overflow: hidden;
    /*    */
    text-overflow: ellipsis;
    /*   '...'  */
    white-space: nowrap;
    /*    */
    border-bottom: 1px solid #f1f1f1;
    /*    */
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_notice-table tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_notice-table tbody tr:nth-child(odd) {
    background-color: #ffffff;
}

/*    */
.wrap .hmk_main-container .hmk_main .hmk_notice-table tbody tr td .hmk_notice-status.read {
    color: #28a745;
    /* :  */
    font-weight: bold;
}

.wrap .hmk_main-container .hmk_main .hmk_notice-table tbody tr td .hmk_notice-status.new {
    color: #dc3545;
    /*  :  */
    font-weight: bold;
}

/*   */
.wrap .hmk_main-container .hmk_main .hmk_notice-table tbody tr:hover {
    background-color: #e9ecef;
    /*     */
}

/*    () */
@media (max-width: 768px) {
    .wrap .hmk_main-container .hmk_main .hmk_notice-table thead {
        display: none;
    }

    .wrap .hmk_main-container .hmk_main .hmk_notice-table,
    .wrap .hmk_main-container .hmk_main .hmk_notice-table tbody,
    .wrap .hmk_main-container .hmk_main .hmk_notice-table tr,
    .wrap .hmk_main-container .hmk_main .hmk_notice-table td {
        display: block;
        width: 100%;
    }

    .wrap .hmk_main-container .hmk_main .hmk_notice-table tr {
        margin-bottom: 15px;
    }

    .wrap .hmk_main-container .hmk_main .hmk_notice-table td {
        text-align: right;
        padding-left: 50%;
        position: relative;
    }

    .wrap .hmk_main-container .hmk_main .hmk_notice-table td::before {
        content: attr(data-label);
        position: absolute;
        left: 10px;
        width: 45%;
        padding-right: 10px;
        white-space: nowrap;
        text-align: left;
        font-weight: bold;
    }
}

/* Modal Overlay */
.hmk_notice_modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    /*    */
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    /*     */
}

/* Modal Content */
.hmk_notice_modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    width: 500px;
    max-width: 90%;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
    position: relative;
}

/* Modal Header */
.hmk_notice_modal-header {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    color: #555;
}

/* Modal Buttons */
.hmk_notice_modal-buttons {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
}

.hmk_notice_modal-button {
    padding: 8px 16px;
    margin-left: 10px;
    border: none;
    border-radius: 4px;
    background-color: #3a7afe;
    /*   */
    color: #fff;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease;
}

.hmk_notice_modal-button:hover {
    background-color: #3063d3;
    /*     */
}

/*     */
.hmk_notice-row:hover {
    background-color: #f5f5f5;
    cursor: pointer;
}

/*    */
.hmk_notice-status.read {
    color: #888;
}

.hmk_notice-status.new {
    color: #3a7afe;
    font-weight: bold;
}

hmk_date-filter {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.hmk_date-picker {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    width: 120px;
}

.hmk_date-separator {
    margin: 0 10px;
    color: #666;
}

.hmk_notice-row {
    cursor: pointer;
    transition: background-color 0.2s;
}

.hmk_notice-row:hover {
