}

.hmk_notice-status {
    padding: 4px 8px;
    border-radius: 12px;
    font-size: 12px;
}

.hmk_notice-status.new {
    background-color: #e3f2fd;
    color: #1976d2;
}

.hmk_notice-status.read {
    background-color: #f5f5f5;
    color: #666;
}

