.jm-missioninfo-body {
    width: 80vw; /*   80%    */
    margin: 0 auto; /*   */
    min-height: 100vh;
    box-sizing: border-box;
    padding: 20px; /*     */
    background-color: #ffffff; /*   */
}

.jm-missionsinfo {
  margin-left: 110px; /*   +  padding (120px + 40px) */
  margin-right: 110px; /*   */
  box-sizing: border-box; /*     */
  padding: 0 20px; /*   */
}


/*       */
.yc_challenge_statistics_bar-chart-section {
  /*      */
  display:flex;
  flex-direction: column; /*   */
  align-items: left;;
  margin: 20px 0; /*    */
}

.yc_challenge_statistics_bar-chart {
  width:100%;
    height: 300px; /*    */
    align-self: center;
  
}

/*   ,  */
.jm-mission-history {
  display: flex;
  margin-bottom: 10px;
}

.jm-history-tatle {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 20px;
  margin-top: 20px;
}

.jm-datepicker-container {
  text-align: left;
  margin-top: 20px;
  margin-left: 20px;
  height: 30px;
}

/*   */
.jm-option-box {
  text-align: right;
  flex-grow: 2;
  margin-top: 20px;
}
/*   */
.jm-challenge-select {
  margin-left: 10px;
  padding: 5px;
  border-radius: 5px;
}
/* ,  */

/*   */
.jm-submission-card {
  
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 10px;
  margin-bottom: 10px;
}

.jm-approval-button {
  float: right;
  color: #7e7d7d;
  border: none;
  border-radius: 5px;
  font-size: 15px;
  cursor: pointer;
}

.jm-user-profile-img-card {
    flex-shrink: 0; /*   */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px; /*      */
}

/*   */
.jm-user-profile-container {
  display: flex;
  margin-block: 15px;
}
/*   */
.jm-user-profile-img-card img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
/*   */
.jm-user-profile-name {
  padding-top: 16px;
}
/*  */
.jm-submission-title {
  font-size: 20px;
  font-weight: bold;
}

/*    */
.jm-task-list {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  margin-bottom: 10px;
}
/*   */
.jm-task-card {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  text-align: center;
}
/*  */
.jm-task-title {
  float: left;
  margin-top: 5px;
  font-size: 16px;
  font-weight: bold;
}
/*   */
.jm-eval-type {
  background-color: white;
  color: #5E91A7;
  border: none;
  padding: 5px;
  font-size: 12px;
  border-radius: 3px;
  float: right;
}
.jm-btn-primary:hover {
  background-color: #383E47;
  color: white;
}
/*   */
.jm-task-img {
  width: 100%;
    max-width: 300px; /*    */
    height: auto;
    aspect-ratio: 1/1; /*   */
    object-fit: cover;
    border-radius: 8px;
}

.jm-image-container {
  position: relative;
  display: inline-block;
}

.jm-slider-buttons {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
}

.jm-slider-btn {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  padding: 5px 7px;
  cursor: pointer;
  font-size: 10px;
  border-radius: 50%;
  margin: 0 10px;
}

.jm-slider-btn:hover {
  background-color: rgba(0, 0, 0, 0.7);
}

.jm-slider-btn.prev {
  left: 10px;
}

.jm-slider-btn.next {
  right: 10px;
}

.jm-task-comment-container {
  border: solid 1px #ccc;
  border-radius: 5px;
  margin-top: 5px;
}
/*   */
.jm-task-comment {
  font-size: 14px;
  margin-block: 10px;
}

/*   */
.jm-info-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
/*   */
.jm-modal-content {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 400px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
}

/*   */
.jm-modal-user-profile-container {
  display: flex;
}
/*   */
.jm-modal-user-profile-img-card img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
/*   */
.jm-modal-user-profile-name {
  padding-top: 16px;
  margin-left: 10px;
}
/*  */
.jm-modal-submission-title {
  font-size: 20px;
  font-weight: bold;
}
/*   */
.jm-close-button {
  background: none;
  border: none;
  font-size: 24px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
/*   */
.jm-modal-mission-tatle {
  text-align: left;
  font-size: 25px;
  font-weight: bold;
  color: #383E47;
  margin-bottom: 10px;
}

.jm-modal-task-images {
  position: relative;
  width: 100%;
  max-width: 400px; /*      */
  margin: 0 auto; /*   */
  overflow: hidden; /*    */
}

/*     */
.jm-info-modal-img {
  text-align: center;
}

/*   */
.jm-modal-task-img {
    width: 100%;
    max-width: 400px; /*    */
    height: auto;
    aspect-ratio: 1/1; /*   */
    object-fit: cover;
    border-radius: 8px;
}

.jm-modal-comment-container {
  border: solid 1px #ccc;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  margin-bottom: 15px;
}
/*    */
.jm-info-modal-button {
  text-align: center;
}
/*   */
.jm-info-modal-button-ok {
  background-color: #5E91A7;
  border: none;
  padding: 5px 15px;
  border-radius: 5px;
  color: white;
  margin-right: 10px;
}
/*   */
.jm-info-modal-button-no {
  background-color: #383E47;
  border: none;
  padding: 5px 15px;
  border-radius: 5px;
  color: white;
  margin-right: 10px;
}

/*   */
.jm-modal-slider-buttons {
  position: absolute;
  top: 50%;
  width: 100%;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  pointer-events: none;
}

.jm-modal-slider-btn {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 18px;
  pointer-events: all;
  transition: background-color 0.3s ease;
}

.jm-modal-slider-btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.jm-modal-slider-btn.prev {
  left: 10px;
}

.jm-modal-slider-btn.next {
  right: 10px;
}

