@charset "UTF-8";

/*  */
.dy-link {
    color: black; 
    text-decoration: none; /*   */
}

.dy-link:visited {
    color: black; 
}

.dy-link:hover {
    text-decoration: none; /*   */
}

.wrap {
    margin: 0 auto;

}

/*    */
.dy-pwsearch {
    width: 600px;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;

}

/*  */
.dy-pwsearch .dy-pwsearchTitle {
    font-size: 30px;
    margin-bottom: 30px;
}

/*    */
.dy-pwsearch .dy-pwsearch-content {
    width: 400px;
    margin: 0 auto;
    margin-top: 30px;

}

/*   , input,  */
.dy-pwsearch .dy-pwsearch-content .dy-pwsearch-search {
    text-align: left;
    margin-top: 7px;

}

/* input  */
.dy-pwsearch-content .dy-pwsearch-search .dy-search-input {
    width: 100%;
    height: 35px;
    border: 1px solid black;
    margin-bottom: 5px;
}

/*  */
.dy-pwsearch-content .dy-pwsearch-search .dy-message {
    text-align: left;
    font-size: 13px;
    margin-bottom: 15px;
}


/*   */
.dy-pwsearch-content .dy-next-btn {
    width: 100%;
    height: 35px;
    border-radius: 15px;
    margin-top: 5px;
    margin-bottom: 40px;

    background-color: #5E91A7;
    color: white;
    border: #497B91;
    cursor: pointer;
}


/*   */
.dy-pwsearch-content .dy-ok-btn {
    width: 100%;
    height: 35px;
    border-radius: 15px;
    margin-top: 20px;
    margin-bottom: 30px;

    background-color: #5E91A7;
    color: white;
    border: #497B91;
    cursor: pointer;
}

