/* yc_css_challenge_sidebar.css */
/*    */
:root {
    --sidebar-bg: #ffffff;
    --sidebar-border: #e0e0e0;
    --sidebar-text: #333333;
    --sidebar-hover-bg: #f5f5f5;
    --sidebar-hover-text: #007aff;
    --sidebar-active-bg: #007aff;
    --sidebar-active-text: #ffffff;
    --modal-bg: #ffffff;
    --modal-overlay: rgba(0, 0, 0, 0.3);
    --button-bg: #007aff;
    --button-hover-bg: #005bb5;
    --button-text: #ffffff;
    --error-color: #e74c3c;
    --success-color: #27ae60;
    --font-family: 'Roboto', sans-serif;
}
/*   */
.yc-challenge-sidebar_sidebar {
    position: fixed;
    top: 20px;
    left: calc((100vw - 80vw) / 2);
    width: 120px;
    max-height: calc(100vh - 350px); /*    Footer   */
    height: auto;
    display: flex;
    flex-direction: column;
    padding: 20px 10px;
    box-sizing: border-box;
    overflow-y: auto;
    z-index: 1000;
    transition: background-color 0.3s ease;
    border-radius: 8px;
    margin-top: 70px;
    margin-bottom: 65px;
    opacity: 0;
    animation: fadeSlideIn 0.5s forwards; /*    */
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    background: linear-gradient(135deg, #ffffff, #f7f9fa); /*   */


}

/*   */
.yc-loading-spinner_sidebar {
    text-align: center;
    font-size: 16px;
    color: #555;
    padding: 20px 0;
    animation: fadeIn 1s forwards;
}

/*   */
.yc-error-message_sidebar {
    color: #e74c3c;
    background-color: #fdecea;
    padding: 6px;
    border-radius: 6px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-size: 14px;
    animation: fadeIn 0.5s forwards;
}

/*   */
.yc-challenge-menu_sidebar {
    background-color: transparent; /*   */
    flex-grow: 1; /*     */
    margin-bottom: 20px;
    border-radius: 8px;
    /* height: 80vh; */ /* : flex-grow  */
}

/*   */
.yc-challenge-menu_sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    border-bottom: 1px solid #353535;
    border-top: 1px solid #353535;
}

/*   */
.yc-challenge-menu_sidebar li {
    padding: 12px 0; /*    */
    margin-bottom: 8px;
    cursor: pointer;
    border-radius: 6px;
    font-size: 14px; /*    */
    color: #000000;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: background-color 0.3s ease, color 0.3s ease, transform 0.2s ease;
    opacity: 0;
    animation: fadeSlideIn 0.5s forwards; /*     */
}

.yc-challenge-menu_sidebar li:first-child{
    margin-top: 10px;
}

/*   */
.yc-menu-text_sidebar {
    margin-top: 5px;
    text-align: center;
    font-size: 12px; /*    */
}

/*    */
.yc-manage-button_sidebar{
    margin-top: 5px;
    text-align: center;
    font-size: 12px; /*    */
}

/*   */
.yc-challenge-menu_sidebar a {
    color: inherit; /*    */
    text-decoration: none; /*   */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

/*    */
.yc-challenge-menu_sidebar li a svg {
    color: inherit; /*     */
    width: 24px;
    height: 24px;
}

/*       */
.yc-challenge-menu_sidebar li:not(.yc-disabled_sidebar):hover,
.yc-challenge-menu_sidebar li.active:not(.yc-disabled_sidebar) {
    background-color: var(--sidebar-hover-bg);
    color: var(--sidebar-hover-text);
    transform: translateY(-2px);
}

/*      */
.yc-disabled-link_sidebar {
    pointer-events: none; /*   */
    color: gray;
    cursor: default;
}

.yc-disabled-link_sidebar .yc-menu-text_sidebar,
.yc-disabled-link_sidebar svg {
    color: gray;
}

/*  li   */
.yc-challenge-menu_sidebar li.yc-disabled_sidebar {
    cursor: default;
}

.yc-challenge-menu_sidebar li.yc-disabled_sidebar:hover {
    background-color: transparent; /*    */
    color: gray; /*    */
    transform: none; /*    */
}

/*    */
.yc-challenge-menu_sidebar li.yc-disabled_sidebar svg {
    color: gray;
}

/*   */
.yc-custom-modal_sidebar {
    
    background: white;
    border-radius: 16px;
    width: 600px;
    max-height: 80vh; /*      80%  */
    text-align: center;
    box-shadow: 0 12px 24px rgba(0, 0, 0, 0.3);
    animation: slideDown 0.6s ease-out, fadeIn 0.3s forwards;
    opacity: 0;
    overflow: auto; /*        */
    z-index: 1001; /*   z-index  */
}


.yc-modal-content_sidebar {
    position: relative; /*         */
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 80vh; /*     */
    overflow-y: auto; /*    */
    padding-top: 50px; /*     */
    margin-bottom: 20px;

}

/*   */
.yc-custom-overlay_sidebar {
    position: fixed; /* ensure overlay covers the entire viewport */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    opacity: 0;
    animation: modalFadeIn 0.3s forwards;
}

/*    */
.yc-close-modal-button_sidebar {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent; /*    */
    color: rgb(0, 0, 0);
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 30px;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.yc-close-modal-button_sidebar:hover {
    transform: scale(1.05);
}

/*      */
.yc-modal-content_sidebar .yc-manage-sections_sidebar {
    padding-top: 20px; /*       */
}

/*   */
.yc-modal-content_sidebar h2 {
    margin-bottom: 20px;
    color: #34495e;
    animation: fadeIn 0.5s forwards;
    font-size: 20px;
}

/*   */
.yc-manage-sections_sidebar {
    width: 80%;
}

.yc-manage-section_sidebar {
    margin-bottom: 20px;
    opacity: 0;
    animation: fadeInSection 0.5s forwards;
}

.yc-manage-section_sidebar h3 {
    margin-bottom: 10px;
    color: #2c3e50;
    font-size: 17px;
    text-align: left;
}

.yc-form-group_sidebar {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
    animation: fadeIn 0.5s forwards;
}

.yc-form-group_sidebar label {
    margin-bottom: 5px;
    font-weight: bold;
    color: #555;
    font-size:15px;
}

.yc-form-group_sidebar input,
.yc-form-group_sidebar select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 14px;
}

.yc-image-preview_sidebar img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-top: 10px;
    border-radius: 8px;
    border: 1px solid #ccc;
    animation: fadeIn 0.5s forwards;
}

/*   */
.yc-update-button_sidebar {
    padding: 8px 16px;
    background-color: #498fc8;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    width: 200px;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.yc-update-button_sidebar:hover {
    background-color: #1976d2;
    transform: scale(1.05);
}

.yc-update-button_sidebar:disabled {
    background-color: #90caf9;
    cursor: not-allowed;
}

/*   */
.yc-success-message_sidebar {
    color: #27ae60;
    background-color: #e8f5e9;
    padding: 8px;
    border-radius: 6px;
    margin-top: 10px;
    font-size: 14px;
    animation: fadeIn 0.5s forwards;
}

/*    */
.yc-modal-buttons_sidebar {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
    animation: fadeIn 0.5s forwards;
}

/*   */
.yc-confirm-button_sidebar {
    padding: 8px 16px;
    background-color: #007bff88;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.yc-confirm-button_sidebar:hover {
    background-color: #007aff;
    transform: scale(1.05);
}

/*   */
.yc-cancel-button_sidebar {
    padding: 8px 16px;
    background-color: #ae514787;
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
}

.yc-cancel-button_sidebar:hover {
    background-color: #c0392b;
    transform: scale(1.05);
}

/*    */
.yc-challenge-footer-buttons_sidebar {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-top: auto; /*   */
    animation: fadeIn 0.5s forwards;
}

/*   */
.yc-challenge-report-btn_sidebar {
    background-color: transparent; /*   */
    color: #c0392b; /*    */
    border: none;
    border-radius: 8px;
    width: 100%;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.3s ease, transform 0.2s ease;
}

.yc-challenge-report-btn_sidebar:hover {
    color: #a32415;
    transform: translateY(-2px);
}

/* Exit  */
.yc-challenge-exit-btn_sidebar {
    background-color: transparent; /*   */
    color: #383E47; /*    */
    border: none;
    border-radius: 8px;
    width: 100%;
    cursor: pointer;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: color 0.3s ease, transform 0.2s ease;
}

.yc-challenge-exit-btn_sidebar:hover {
    color: #2c2f33;
    transform: translateY(-2px);
}

/*      */
.yc-disabled-link_sidebar {
    pointer-events: none; /*   */
    color: gray;
    cursor: default;
}

.yc-disabled-link_sidebar .yc-menu-text_sidebar,
.yc-disabled-link_sidebar svg {
    color: gray;
}

/* Animations */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeSlideIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes slideDown {
    from {
        transform: translateY(-30px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes slideUp {
    from {
        transform: translateY(0);
        opacity: 1;
    }
    to {
        transform: translateY(-30px);
        opacity: 0;
    }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
        transform: translateY(0);
    }
    40% {
        transform: translateY(-10px);
    }
    60% {
        transform: translateY(-5px);
    }
}

@keyframes fadeInSection {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

