@charset "UTF-8";

/*  */
.dy-link {
    color: black; 
    text-decoration: none; /*   */
}

.dy-link:visited {
    color: black; 
}

.dy-link:hover {
    text-decoration: none; /*   */
}

.wrap {
    margin: 0 auto;

}

/*   */
.dy-loginform {
    width: 600px;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;

}

/*  */
.dy-loginform .dy-loginTitle {
    font-size: 30px;
    margin-bottom: 30px;
}


/*    */
.dy-loginform .dy-loginform-content {
    width: 400px;
    margin: 0 auto;
    margin-top: 30px;

}


/*    */
.dy-loginform .dy-api-logins {
    margin-bottom: 30px;
}

/*    */
.dy-loginform .dy-kakao-login {
    width: 300px;
    height: 45px;
    cursor: pointer;
    /* display: flex; 
    justify-content: center;
    align-items: center; */
    
    /* border: 1px solid black; */
    border-radius: 12px;
    margin-bottom: 10px;
}

/*  :    */
.dy-social-login {
    width: 300px;
    height: 45px;
    margin: 10px auto;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: bold;
    color: white;
    cursor: pointer;
    text-align: center;
}

/*     */
.dy-social-login.kakao {
    background-color: #fee500;
    color: #3c1e1e;
    border: 1px solid #e6b800;
    margin-bottom: 0;
}

.dy-social-login.google {
    background-color: #ffffff;
    color: white;
    border: none;
    margin-top: 0;
}

.dy-social-login.naver {
    background-color: #03c75a;
    color: white;
    border: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 3px 0 0 6px;
    gap: 70px;
    margin-top: 12px;


}

/* hover  */
.dy-social-login:hover {
    opacity: 0.9;
}

.dy-loginform .dy-loginform-content .dy-middle {
    margin-bottom: 30px;
}

/*   , input */
.dy-loginform .dy-loginform-content .dy-loginform-login {
    text-align: left;

}

/* input  */
.dy-loginform-content .dy-loginform-login .dy-login-input {
    width: 98%;
    height: 35px;
    border-radius: 15px;
    border: 1px solid black;
    margin-top: 5px;
    margin-bottom: 5px;
    padding-left: 5px;
}

.dy-loginform-content .dy-loginform-login .dy-error-message {
    color: #e32200;
}

/*   */
.dy-loginform-content .dy-submit-btn {
    width: 100%;
    height: 35px;
    border-radius: 15px;
    margin-top: 30px;

    background-color: #5E91A7;
    color: white;
    border: #497B91;
}

.dy-loginform-content .dy-submit-btn:hover {
    cursor: pointer;
}

/*  */
.dy-loginform-content .dy-password-search {
    margin-top: 30px;
}

/*  */
.dy-loginform-content .dy-to-joinform {
    margin-top: 10px;
    padding-bottom: 40px;
}

