@charset "UTF-8";

/*  */
.dy-link {
    color: black; 
    text-decoration: none; /*   */
}

.dy-link:visited {
    color: black; 
}

.dy-link:hover {
    text-decoration: none; /*   */
}

.wrap {
    margin: 0 auto;

}

/*    */
.dy-pwalter {
    width: 600px;
    margin: 0 auto;
    margin-top: 70px;
    text-align: center;

}

/*  */
.dy-pwalter .dy-pwalterTitle {
    font-size: 30px;
    margin-bottom: 30px;
}

/*    */
.dy-pwalter .dy-pwalter-content {
    width: 400px;
    margin: 0 auto;
    margin-top: 30px;

}

/*   , input,  */
.dy-pwalter .dy-pwalter-content .dy-pwalter-alter {
    text-align: left;
    margin-top: 7px;

}

/* input  */
.dy-pwalter-content .dy-pwalter-alter .dy-alter-input {
    width: 100%;
    height: 35px;
    border: 1px solid black;
    margin-bottom: 5px;
}

/*  */
.dy-pwalter-content .dy-pwalter-alter .dy-message {
    text-align: left;
    font-size: 13px;
    margin-bottom: 15px;
}

.dy-joinform-join .dy-message ol {
    font-size: 12px;
}

/*   */
.dy-pwalter-content .dy-submit-btn {
    width: 100%;
    height: 35px;
    border-radius: 15px;
    margin-top: 20px;
    margin-bottom: 30px;
    cursor: pointer;

    background-color: #5E91A7;
    color: white;
    border: #497B91;
}


