@charset "UTF-8";

/* ham_M_notice.css */

/*    */
.hmk_mobile_notice-container {
    padding: 16px;
    padding-bottom: 80px;
    /*        */
    background-color: #f2f2f7;
    display: flex;
    flex-direction: column;
    overflow-x: hidden;
    /*    */
    box-sizing: border-box;
    height: calc(100vh - 23px - 64px);
    /*     (180px)   (80px)  */
}

/*     */
.hmk_mobile_notice-summary {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    margin-bottom: 20px;
}

/*    */
.hmk_mobile_notice-card {
    background: white;
    border-radius: 12px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

/*    */
.hmk_mobile_notice-card-label {
    font-size: 14px;
    color: #666;
    margin-bottom: 8px;
}

/*    */
.hmk_mobile_notice-card-value {
    font-size: 20px;
    font-weight: 600;
    color: #333;
}

/*    */
.hmk_mobile_notice-tabs {
    display: flex;
    background: white;
    border-radius: 12px;
    padding: 4px;
    margin-bottom: 16px;
    box-sizing: border-box;
    width: 100%;
    overflow-x: auto;
    /*        */
}

/*    */
.hmk_mobile_notice-tab {
    flex: 1;
    padding: 10px;
    border: none;
    background: none;
    font-size: 14px;
    color: #666;
    border-radius: 8px;
    white-space: nowrap;
    flex-shrink: 0;
    /*     */
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

/*    */
.hmk_mobile_notice-tab.active {
    background: #007AFF;
    color: white;
}

/*    */
.hmk_mobile_notice-list {
    flex: 1;
    /*     */
    display: flex;
    flex-direction: column;
    gap: 12px;
    padding-bottom: 16px;
    /*      */
    overflow-y: auto;
    /*    */
    -webkit-overflow-scrolling: touch;
    /* iOS   */
    box-sizing: border-box;
    width: 100%;
    scrollbar-width: none;
    /* Firefox */
}

/*   */
.hmk_mobile_notice-list::-webkit-scrollbar {
    display: none;
    /* Chrome, Safari, Opera */
}

/*    */
.hmk_mobile_notice-item {
    background: white;
    border-radius: 12px;
    padding: 16px;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    width: 100%;
}

/*    */
.hmk_mobile_notice-item-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

/*    */
.hmk_mobile_notice-item-title {
    font-size: 16px;
    font-weight: 500;
    color: #333;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

/*    */
.hmk_mobile_notice-item-status {
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 12px;
}

/*    */
.hmk_mobile_notice-item-status.new {
    background: #007AFF;
    color: white;
}

/*    */
.hmk_mobile_notice-item-status.read {
    background: #E5E5EA;
    color: #666;
}

/*    */
.hmk_mobile_notice-item-sender {
    font-size: 14px;
    color: #666;
    margin-bottom: 4px;
}

/*    */
.hmk_mobile_notice-item-date {
    font-size: 12px;
    color: #999;
}

/*      */
.hmk_mobile_notice-empty {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #999;
    font-size: 15px;
    margin-bottom: 80px;
    /*    +   */
}

/*    */
.hmk_mobile_notice-modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    overflow: hidden;
    /*     */
}

/*    */
.hmk_mobile_notice-modal {
    background: white;
    border-radius: 12px;
    width: 90%;
    max-width: 400px;
    padding: 20px;
    max-height: 80vh;
    overflow-y: auto;
    /*       */
    box-sizing: border-box;
}

/*   */
.hmk_mobile_notice-modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    border-bottom: 1px solid #999;
}

/*   */
.hmk_mobile_notice-modal-header h2 {
    font-size: 1.2rem;
    font-weight: 500;
    margin: 0;
}

/*    */
.hmk_mobile_notice-modal-close {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0;
}

/*   */
.hmk_mobile_notice-modal-content {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

/*   */
.hmk_mobile_notice-modal-message {
    color: #333;
    line-height: 1.5;
}

/*    */
.hmk_mobile_notice-modal-info {
    display: flex;
    flex-direction: column;
    text-align: right;
    gap: 8px;
    color: #666;
    font-size: 0.9rem;
}

/*     */
.hmk_mobile_notice-modal-room-button {
    background-color: #3b82f6;
    color: white;
    border: none;
    padding: 12px;
    border-radius: 6px;
    cursor: pointer;
    margin-top: 16px;
    transition: background-color 0.3s ease;
}

.hmk_mobile_notice-modal-room-button:hover {
    background-color: #2563eb;
}