.jm-challenge-point-info {
position: absolute; /*    */
  top: 250px; /* TopHeader    */
  right: 230px; /*   */
  width: 150px;
  height: auto;
  background-color: #ffffff; /*  */
  border: 1px solid #ddd; /*  */
  border-radius: 10px; /*   */
  padding: 10px; /*   */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); /*   */
  z-index: 1000; /*     */
}

.jm-challenge-point-info h2 {
    font-size: 14px;
    font-weight: bold;
    margin-bottom: 10px;
}

.jm-challenge-point-info p {
    font-size: 12px;
    color: #8d8d8d;
    margin-bottom: 10px;
}