@charset "UTF-8";

/*    (Chrome, Safari ) */
.ham_wrap::-webkit-scrollbar {
    width: 10px; /*   */
    height: 10px; /*    */
}

.ham_wrap::-webkit-scrollbar-track {
    background: #fff; /*   */
    border-radius: 10px; /*   */
}

.ham_wrap::-webkit-scrollbar-thumb {
    background: #f2f8f9; /*   */
    border-radius: 10px; /*   */
    border: 3px solid #fff; /*     */
}

.ham_wrap::-webkit-scrollbar-thumb:hover {
    background: #e0e0e0; /*      */
}


/**************************************<  >***********************************/
.wrap .hmk_header {
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #e0e0e0;
    background-color: #f8f9fa;
    position: relative;
    padding: 0 20px;
}

/*    */
.wrap .hmk_header .hmk_logo {
    position: absolute;
    left: 20px;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    /* background-color: gray; */
}

/*    */
.wrap .hmk_header .hmk_menu {
    display: flex;
    gap: 20px;
    list-style: none;
}

.hmk_header .hmk_menu ul {
    display: flex;
    gap: 20px;
    list-style: none;
}

.wrap .hmk_header .hmk_menu li a {
    text-decoration: none;
    color: #333;
    font-weight: 600;
}

/*        */
.wrap .hmk_header .hmk_menu2 {
    position: absolute;
    right: 40px;
    display: flex;
    align-items: center;
    gap: 10px;
    top: 50%;
    transform: translateY(-50%);
}

.wrap .hmk_header .hmk_menu2 ul {
    display: flex;
    align-items: center;
    gap: 10px;
    list-style: none;
}

.wrap .hmk_header .hmk_menu2 img {
    width: 40px;
    height: 40px;
    border-radius: 50%;
}

.wrap .hmk_header .hmk_menu2 p {
    font-size: 16px;
    font-weight: bold;
    margin: 0;
}

.wrap .hmk_header .hmk_menu2 span {
    font-size: 14px;
    color: #666;
}
/**************************************< / >***********************************/
.ham_wrap {
    box-sizing: border-box;
    font-family: "Pretendard Variable", Pretendard, sans-serif;
    overflow-x: hidden; /*     */
    margin: 0; /*    */
    padding: 0;
    width: 80vw;
    min-height: 100vh;
    margin-left: 187px;
    box-sizing: border-box; /*     */
    scrollbar-width: thin; /*   */
    scrollbar-color: #e0e0e0 #f0f0f0; /*      */
}

/************************* <   > *********************/
.wrap .hmk_main-container {

    display: flex;
    margin-top: 10px;
    background-color: #fff;
}
/************************* < /  > *********************/

/********************   :     ********************/
.wrap .hmk_main .hmk_stat-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px; /*    */
    justify-content: center; /*    */
    padding: 20px;
    background-color: #fff;
    border-radius: 8px; /*   */
}

.chart-wrapper {
    width: 160px; /*    */
    height: 160px; /*    */
}

/*    */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card {
    width: 300px;
    min-width: 288px;
    height: 150px;
    padding: 15px;
    background-color: white;
    border-radius: 12px; /*    */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /*    */
    display: flex;
    flex-direction: row; /*     */
    align-items: center; /*    */
    text-align: left; /*    */
}

/*    */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_chart {
    position: relative;
    flex-shrink: 0; /*    */
}

/*      */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_stat-card_chart-center-text {
    /*      */
    position: absolute;
    top: 40%;
    left: 33%;
    /*     ... */
    font-size: 12px;
    color: #333;
    text-align: center;
    /*    */
    font-weight: bold;
    /*    */
}

/*    */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_stat-info {
    display: flex;
    flex-direction: column; /*    */
    justify-content: center; /*    */
    overflow-wrap: break-word;
    word-break: keep-all;
    line-height: 1.5;
}

/*    */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_stat-title {
    font-size: 16px; /*      */
    font-weight: 600;
    color: #333;
    margin: 0 0 5px 0;
}

/*    */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_stat-data {
    font-size: 14px; /*      */
    color: #666;
    margin: 0 0 10px 0;
}

/*  */
.wrap .hmk_main .hmk_stat-container .hmk_stat-card .hmk_stat-link {
    font-size: 14px; /*      */
    color: #3a7afe;
    text-decoration: none;
    font-weight: 500;
}

/*   ( ) */
@media (max-width: 1024px) {
    .wrap .hmk_main .hmk_stat-container .hmk_stat-card {
        width: 45%; /*      */
    }
}

@media (max-width: 600px) {
    .wrap .hmk_main .hmk_stat-container .hmk_stat-card {
        width: 100%; /*      */
    }
}

/*      */
.wrap .hmk_main-container .hmk_main .hmk_tab-menu {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_tab-menu .hmk_tab-button {
    padding: 10px 20px;
    font-size: 16px;
    font-weight: bold;
    color: #666;
    background-color: #f1f1f1;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, color 0.3s;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_tab-menu .hmk_tab-button:hover {
    background-color: #e0e0e0;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_tab-menu .hmk_tab-button.hmk_active {
    background-color: #3a7afe;
    color: #ffffff;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_tab-menu .hmk_tab-button.hmk_active:hover {
    background-color: #3065d0;
    color: #ffffff;
}

/*     */
.wrap .hmk_main-container .hmk_main .hmk_challenge-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    margin-top: 20px;
}

/*   */
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card {
    display: flex;
    flex-direction: row;
    width: 45%;
    max-width: 500px;
    min-width: 395px;
    background-color: #ffffff;
    border-radius: 10px; /*     */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s;
    padding: 10px; /*     */

}

/*   ( ) */
@media (max-width: 1024px) {
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card {
        width: 25%; /*      */
    }
}

@media (max-width: 600px) {
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card {
        width: 80%; /*      */
    }
}

.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card:hover {
    transform: translateY(-5px); /*        */
}

/*      */
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card 
.hmk_challenge-image {
    width: 100px;
    height: 100px;
    object-fit: cover;
    margin-right: 15px; /*     */
    border-radius: 8px; /*    */
    flex-shrink: 0; /*         */
}

/*     */
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card 
.hmk_challenge-details {
    padding: 10px 0; /*    */
    flex: 1;
}

.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card 
.hmk_challenge-details
.hmk_challenge-datebox {
    display: flex;
    align-items: center;
    gap: 20px;
    color: #666;
    margin-bottom: 8px;
}

/*      */
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card 
.hmk_challenge-details
.hmk_challenge-date {
    font-size: 14px;
    color: #666;
    margin-bottom: 8px;
    font-weight: bold;
}

/*     */
.wrap .hmk_main-container 
.hmk_main 
.hmk_challenge-list 
.hmk_challenge-card 
.hmk_challenge-details
.hmk_challenge-title {
    font-size: 18px;
    font-weight: bold;
    color: #333;
    margin-top: 4px;
    line-height: 1.4;
}


/*      */
.hmk_challenge-card.completed {
    position: relative;
    opacity: 0.8;
}

.hmk_challenge-card.completed::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 12px;
}